<template>
  <div id="app">
    <message v-if="msgState"></message>
    <topBar></topBar>
    <classify></classify>
     <router-view class="view_content"></router-view>
  </div>
</template>
<script>
  import topBar from './components/topBar.vue'
  import classify from './components/classify.vue'
  import message from './components/message.vue'
  import {mapGetters} from 'vuex';
  export default {
      name: 'app',
      components:{
        topBar,classify,message
      },
      computed:mapGetters(
              ['msgState']
      ),
      watch:{
      $route(to){
          let path = to.path.substring(1);
          console.log('路由变化',path);

          if(path=='search'||path=='channel'){
              this.$store.dispatch('topHide');
              this.$store.dispatch('clsHide');
          }else {
              this.$store.dispatch('topShow');
              this.$store.dispatch('clsShow');
          }     //进入频道管理或搜索     顶部隐藏

          if(path=='message'){
              this.$store.dispatch('msgShow')
          }else {
              this.$store.dispatch('msgHide')
          }             //进入消息页控制显示隐藏
      }
    },
      mounted(){
        $('.refresh').click(function () {
            JSON.parse(localStorage.haveList).forEach(function (val,index) {
                if(val.to==this.$route.path){
                    this.$refreshData(val.url)
                }else if(this.$route.path=='/recommend'){
                   this.$refreshData('http://m.toutiao.com/list/?tag=__all__&ac=wap&count=20&format=json_raw&as=A155C88F615D781&cp=58F12D67A8817E1&min_behot_time=')
                }
            }.bind(this))
        }.bind(this));
      }
}
</script>

<style lang="scss">

</style>



