<!--推荐-->
<template>
    <div class="news">
        <a v-for="(val,index) in $root.$data.arr" :href="val.article_url" :key="index" class="everyNews"  :class="[{article:val.image_list.length==3},{advert:val.ad_label=='广告'},{has_video:val.has_video==true},{no_Img:val.image_list.length==0}]" v-show="val.has_video!=true">
                <h1 class="newsTit">{{val.title}}</h1>
                <ul class='imgList'>
                    <li v-for="(v,i) in val.image_list"><img :src="v.url"></li>
                </ul>
                <p class="news_btm">
                    <span class="media_name">{{val.media_name}}</span>
                    <span class="comment_count">评论{{val.comment_count}}</span>
                    <span class="datetime">{{val.datetime}}</span>
                </p>
            </a>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                arr:[],
                url:'http://m.toutiao.com/list/?tag=__all__&ac=wap&count=20&format=json_raw&as=A155C88F615D781&cp=58F12D67A8817E1&min_behot_time=',
                arriveBtm:false
            }
        },
        methods:{
            btmDetect(){
                $(window).scroll(function () {
                    if(($(document).height()-$(window).height()-$(document).scrollTop())<5) this.$getData(this.url);
                }.bind(this))
            },
        },
        mounted(){
            this.$refreshData(this.url);
            this.btmDetect();
        }
    }
</script>
<style lang="scss">
    @import '../assets/css/public.scss';
</style>


