import * as types from './types';

export default{                 
    msgShow({commit,state}){
        commit(types.MSG_SHOW)
    },
    msgHide({commit,state}){
        commit(types.MSG_HIDE)
    },
    choiceShow({commit,state}){
        commit(types.CHOICE_SHOW)
    },
    choiceHide({commit,state}){
        commit(types.CHOICE_HIDE)
    },
    clsShow({commit,state}){
        commit(types.CLS_SHOW)
    },
    clsHide({commit,state}){
        commit(types.CLS_HIDE)
    },
    topShow({commit,state}){
        commit(types.TOP_SHOW)
    },
    topHide({commit,state}){
        commit(types.TOP_HIDE)
    }
    
}


