<!--视频-->
<template>
    <div class="video news">
        <a v-for="(val,index) in $root.$data.arr" :href="val.article_url" :key="index" class="everyNews"  v-if='val.ad_label!="广告"'>
              <h2 class="videoTit" v-text="val.title"></h2>
            <img class="playBtn" src="http://s3a.pstatp.com/growth/mobile_list/image/playicon_video@3x_dcf536ff888c86df29f147413a7158ca.png">
            <a class="large_image">
                <p class="videoMask"></p>
                <img :src="val.large_image_url||val.video_detail_info.detail_video_large_image.url">
            </a>
            <p class="news_btm">
                <span class="media_name">{{val.media_name}}</span>
                <span class="comment_count">评论{{val.comment_count}}</span>
                <span class="datetime">{{val.datetime}}</span>
            </p>
        </a>
    </div>
</template>
<style>

</style>
<script>
    export default{
        data(){
            return{
                url:'http://m.toutiao.com/list/?tag=video&ac=wap&count=20&format=json_raw&as=A185288FD14D292&cp=58F1BDC2E9528E1&min_behot_time=',
                arr:[],
                arriveBtm:false
            }
        },
        methods:{
            btmDetect(){
                $(window).scroll(function () {
                    if(($(document).height()-$(window).height()-$(document).scrollTop())<5) this.$getData(this.url);
                }.bind(this))
            }
        },
        mounted(){
            this.$refreshData(this.url);
            this.btmDetect();
        }

    }
</script>
<style lang="scss">
    .video{
        overflow: hidden;

        .everyNews{
            position: relative;
             .videoTit{
                 z-index: 5;
                 padding: 2rem 2rem 0 1rem;
                font-size: 2.1rem;
                font-weight: normal;
                color: #fff;
                position: absolute;
            }
            .playBtn{
                z-index: 5;
                position: absolute;
                width: 7rem;
                height: 7rem;
                left: 50%;
                top: 50%;
                margin-left: -3.5rem;
                margin-top: -3.5rem;
        }
            .large_image{
                display: block;
                position: relative;
                .videoMask{
                    width: 100%;
                    height: 32.1rem;
                    margin-top: 1rem;
                    position: absolute;
                    top: 0;
                    background: rgba(0,0,0,.3);
                }
               img{
                   padding: 1rem 0;
                   width: 100%;
                   height: 32.1rem;
               }
            }
        }

    }
</style>


