<!--搜索-->
<template>
    <div class="search">
        <div class="search_top">
          <a class="goBack iconfont" href="javascript:window.history.go(-1)">&#xf0292;</a><span>搜索</span>
        </div>
        <p class="ipt">
            <a href="javascript:" class="engine small_news"></a>
            <span class="corner iconfont">&#xe650;</span>
            <input type="text" placeholder="请输入搜索关键词" v-model="iptValue" @keydown.13="search">
        </p>
        <p class="hotWords">
            <span v-for="(val,index) in arr"><a :href="val.detailUrl">{{val.title}}</a></span>
        </p>
        <div class="choicePage" v-show="choiceState">         <!--引擎选择-->
            <div class="choiceBox">
                <span class="iconfont">&#xe650;</span>
                <ul>
                    <li class="tdNews">新闻</li>
                    <li class="godHorses">神马</li>
                    <li class="baidu">百度</li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';
    export default{
        data(){
            return{
                keywordsUrl:'http://m.toutiao.com/baidu_top_words/?csrfmiddlewaretoken=undefined',
                locationUrl:'/src/assets/data/keywords.json',
                arr:[],
                iptValue:'',
                baiduUrl:'https://www.baidu.com/s?wd=',
                godhsUrl:'http://m.yz.sm.cn/s?q=',
                newsUrl:'http://m.toutiao.com/search/?keyword='
            }
        },
        methods:{
                getKeywords(){
                    this.$http.get(this.locationUrl).then(function(res){
                        this.arr = res.data.data;
                        console.log(this.arr)
                    }.bind(this)).catch(function (res) {
                        console.log(res.state)
                    })
                },
                choiceShowCtr(){
                    let arr = [];                       //创建空数组存放rem单位的元素宽高
                    arr.push($('.choiceBox').width(),$('.choiceBox').height())

                    $('.choiceBox').css({width:0});            //元素宽高置0
                    $('.choiceBox').css({height:0});

                    $('.engine').click(function (eve) {
                        if(!this.$store.getters.choiceState){
                            console.log('执行显示');
                            this.$store.dispatch('choiceShow')
                            $('.choiceBox').stop(true).animate({opacity:1,width:arr[0],height:arr[1]})
                        }
                        eve.stopPropagation();
                    }.bind(this));

                    $(document).click(function (eve) {
                        if(this.$store.getters.choiceState){
                            console.log('执行隐藏');
                            if(eve.toElement.className=='tdNews') $('.engine').addClass('small_news').removeClass('small_baidu').removeClass('small_godhs');
                            if(eve.toElement.className=='godHorses') $('.engine').addClass('small_godhs').removeClass('small_baidu').removeClass('small_news');
                            if(eve.toElement.className=='baidu') $('.engine').addClass('small_baidu').removeClass('small_news').removeClass('small_godhs');
                            $('.choiceBox').animate({opacity:0,width:0,height:0},function () {
                                this.$store.dispatch('choiceHide')
                            }.bind(this));
                        }
                    }.bind(this));
                },
                search(){
                    if($('.engine').attr('class').indexOf('small_news')!=-1){
                        if(this.iptValue==''){
                            this.$router.replace({path:'/search'})
                        }else if(this.iptValue){
                            window.location.href = this.newsUrl+this.iptValue;
                        }
                        console.log(1)
                    }else  if($('.engine').attr('class').indexOf('small_godhs')!=-1){
                        window.location.href = this.godhsUrl+this.iptValue;
                        console.log(2)
                    }else {
                        window.location.href = this.baiduUrl+this.iptValue;
                        console.log(3)
                    }
                },
                ...mapActions(
                        ['choiceShow','choiceHide']
                )
//            getKeywords(){
//                this.$http.jsonp(this.keywordsUrl).then(function (res) {
//                    console.log(res)
//                }.bind(this)).catch(function (res) {
//                    console.log(res);
//                })
//            }
        },
        mounted(){
            this.getKeywords();     //获取热门关键词       接口有问题
            this.choiceShowCtr()        //搜索引擎 选择控制显示隐藏

        },
        computed:mapGetters(
                ['choiceState']
        )
    }
</script>
<style lang="scss">
    .search{
        z-index: 99;
        width: 100%;
        padding-top:0;
        position:absolute;
         top:0;
        background:#fff;
        .search_top{
            height: 7rem;
            background: #D43D3D;
            font-size: 3rem;
            line-height: 7rem;
            color: #fff;
            font-weight: bold;
            text-align: center;
            .goBack{
                position: absolute;
                top: 0;
                left: 0;
                padding: 0 2rem;
                color: #fff;
                font-weight: bold;
                font-size: 3rem;
            }
        }
        .ipt{
            overflow: hidden;
            position: relative;
            height: 5rem;
            background: #C9C9C9;
             text-align: center;
             line-height:5.6rem;
            .corner{
                color: #9C9C9C;
                position: absolute;
                height: 5rem;
                font-size: 1.8rem;
                top: 0;
                left: 4rem;
            }
            .engine{
                position: absolute;
                display: block;
                width: 4rem;
                height: 3rem;
                top: 1rem;
                left: 1rem;
            }
            .small_news{
                background: url("http://s3.pstatp.com/image/toutiao_mobile/search_icons.png?ver=201408311") no-repeat 0 -6rem ;
                background-size: 3rem;
            }
            .small_godhs{
                background: url("http://s3.pstatp.com/image/toutiao_mobile/search_icons.png?ver=201408311") no-repeat 0 -11.7rem ;
                background-size: 3rem;
            }
            .small_baidu{
                background: url("http://s3.pstatp.com/image/toutiao_mobile/search_icons.png?ver=201408311") no-repeat 0 0 ;
                background-size: 3rem;
            }
            input{
                background: #fff;
                height: 4rem;
                width: 90%;
                border-radius: 5px;
                font-size: 2rem;
                padding-left: 5.5rem;
            }
        }
        .choicePage{
            top: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            background: rgba(0,0,0,.5);
            z-index: 99;
            .choiceBox{
                position:absolute;
                z-index: 199;
                width: 35.3rem;
                height:10.8rem ;
                background: #c9c9c9;
                top:12.7rem;
                left: 1rem;
                border-radius: 10px;
                span{
                    position: absolute;
                    transform: rotateZ(180deg);
                    font-size: 3rem;
                    color: #c9c9c9;
                    top:-1.9rem;
                }
                ul{
                    li{
                        padding-top: 7.8rem;
                        font-size: 2rem;
                        text-align: center;
                        height:3rem ;
                        width: 33%;
                        float: left;
                    }
                    .tdNews{
                        background: url("http://s3.pstatp.com/image/toutiao_mobile/search_icons.png?ver") no-repeat  50% -10.5rem;
                        background-size: 6rem;
                    }
                    .godHorses{
                        background: url("http://s3.pstatp.com/image/toutiao_mobile/search_icons.png?ver") no-repeat 50% -22rem;
                        background-size: 6rem;
                    }
                    .baidu{
                        background: url("http://s3.pstatp.com/image/toutiao_mobile/search_icons.png?ver") no-repeat 50% 1rem;
                        background-size: 6rem;
                    }
                }
            }
        }
    .hotWords{
        overflow: hidden;
        text-align: center;
    span{
        background: #f5f5f5;
        display: inline-block;
        padding: 1rem;
        margin: 1.5rem;
    }
        a{
            font-size: 2rem;
            color: #666;
        }
    }
    }
</style>


