<!--分类-->
<template>
    <div class="classify" >
        <div class="bFixed2">
            <ul>
                <router-link active-class='active'  to='/recommend' tag='li' ><a href="javascript:">推荐</a></router-link>
                <router-link  v-for="(val,index) in $root.haveList" :key="index"  v-cls-ctrl active-class='active'  :to='val.to' tag='li'><a href="javascript:">{{val.title}}</a></router-link>
            </ul>
            <router-link to='/channel' class="add iconfont">&#xe619;</router-link>
        </div>
    </div>
</template>
<style>
    ::-webkit-scrollbar{
      display:none;
    }
</style>
<script>
    import {mapGetters} from 'vuex';
    export default{
        directives:{
            clsCtrl:function (el) {
                $(el).click(function () {
                    console.log(JSON.parse(localStorage.haveList).length)
                    if($(this).index()>=4&&$(this).index()<=(JSON.parse(localStorage.haveList).length-2)){
                        $('.classify ul').animate({left:-$(this).width()*($(this).index()-3)})
                    }
                    else if($(this).index()==(JSON.parse(localStorage.haveList).length-1)||$(this).index()==(JSON.parse(localStorage.haveList).length)){
                        $('.classify ul').animate({left:-$(this).width()*6})
                    }
                     else {
                        $('.classify ul').animate({left:0})
                    }
                })
            }
        },
        computed:mapGetters(
                ['clsState']
        ),
        mounted(){
            $('.classify ul').css({width:$('.classify li').width()*(JSON.parse(localStorage.haveList).length+1)});
            this.$root.haveList = JSON.parse(localStorage.haveList);
        },
    }
</script>
<style lang="scss"  type="text/css">
    .classify{
        height: 5rem;
        position: relative;
        top:6rem;
        a{
            color: #505050;
        }
    }
    .bFixed2{
        width: 100%;
        height: 5rem;
        background:#F4F5F6 ;
    }
    .classify ul{
        position: absolute;
        height:4rem;
        width:auto;
        padding-right: 4rem;
         li{
            float: left;
            margin: 1rem 0;
             a{
                 font-size: 2.5rem;
                 margin:0 1.8rem;
             }
        }
    }
    .add{
        width: 4rem;
        height: 5rem;
        line-height: 5rem;
        text-align: center;
        position: fixed;
        font-size: 3rem;
        color: #F78181;
        top:6rem;
        right:0;
        padding: 0 1rem;
        background: #F4F5F6;
    }
</style>



