import * as types from './types'

export default{
    [types.MSG_SHOW]:(state)=>{
        state.bMsg = true;
    },
    [types.MSG_HIDE]:(state)=>{
        state.bMsg = false;
    },
    [types.CHOICE_SHOW]:(state)=>{
        state.bChoice = true;
    },
    [types.CHOICE_HIDE]:(state)=>{
        state.bChoice = false;
    },
    [types.CLS_HIDE]:(state)=>{
        state.bClassify = false;
    },
    [types.CLS_SHOW]:(state)=>{
        state.bClassify = true;
    },
    [types.TOP_SHOW]:(state)=>{
        state.bTopBar = true;
    },
    [types.TOP_HIDE]:(state)=>{
        state.bTopBar = false;
    }
}




