<!--频道管理-->
<template>
    <div class="channel">
        <div class="channel_top">
            <a class="goBack iconfont" href="javascript:window.history.go(-1)">&#xf0292;</a><span>频道管理</span>
        </div>
        <div class="labelShow">
            <h1>点击删除以下频道</h1>
            <ul class="alreadyHave">
                <li class="label recommend">推荐</li>
                <transition-group enter-active-class="animated zoomIn">

                    <li class="label" v-for="(val,index) in $root.haveList" :key="index">{{val.title}}</li>
                </transition-group>
            </ul>
            <h1>点击添加以下频道</h1>
            <ul class="choosable">
                <transition-group enter-active-class="animated zoomIn">
                      <li v-for="(val,index) in $root.selectableList" :key="index">{{val.title}}</li>
                </transition-group>
            </ul>
        </div>
    </div>
</template>
<style lang="scss">
    .channel{
        width: 100%;
        position: absolute;
        top: 0;
        padding-top: 0;
         z-index: 10;
            h1{
                color: #666;
                font-size: 2rem;
                font-weight: normal;
                padding: 1rem;
                background: #f5f5f5;
            }
         .channel_top {
            height: 7rem;
            background: #D43D3D;
            font-size: 2.5rem;
            line-height: 7rem;
            color: #fff;
            font-weight: bold;
            text-align: center;
            .goBack{
                position: absolute;
                top: 0;
                left: 0;
                padding: 0 2rem;
                color: #fff;
                font-weight: bold;
                font-size: 3rem;
            }
         }
        .labelShow{
            ul{
                overflow: hidden;
                padding: 1rem;
            li.recommend{
                background: #f0f0f0;
            }
            li{
                transform: scale(1);
                float: left;
                font-size: 2.5rem;
                padding: .7rem 3.3rem;
                color: #131313;
                border: 3px #ccc solid;
                margin: 1rem 1.2rem;
            }
            }
        }
    }
</style>
<script>
    export default{
        data(){
            return{
                haveList:[],
                selectableList:[]
            }
        },
        mounted(){
            this.$root.selectableList = JSON.parse(localStorage.selectableList);
            this.$root.haveList = JSON.parse(localStorage.haveList);
            let that =this;
            $('.alreadyHave').click(function (eve) {
                if($(eve.target).text()!='推荐'){
                    that.$root.haveList.forEach(function (val,index) {
                            if(val.title==$(eve.target).text()){
                            let tmpObj = val;
                                that.$root.haveList.splice(index,1);
                                that.$root.selectableList.push(tmpObj);
                            $('.classify ul').css({width:$('.classify li').width()*(that.$root.$data.haveList.length+1)});
                        }
                    }.bind(this))
                }
                localStorage.haveList = JSON.stringify(that.$root.haveList);
                localStorage.selectableList = JSON.stringify(that.$root.selectableList);
            });
            $('.choosable').click(function (eve) {
                that.$root.selectableList.forEach(function (val, index) {
                    if (val.title == $(eve.target).text()) {
                        let tmpObj = val;
                        that.$root.selectableList.splice(index,1);
                        that.$root.haveList.push(tmpObj);
                        $('.classify ul').css({width:$('.classify li').width()*(that.$root.haveList.length+1)});
                    }
                }.bind(this));
                localStorage.haveList = JSON.stringify(that.$root.haveList);
                localStorage.selectableList = JSON.stringify(that.$root.selectableList);
            });
        }
    }
</script>


