export const getData = function(url){
    $('.refresh').addClass('refreshing');
    // let time = parseInt(new Date().getTime()/1000);
    // url += time;
    
    this.$http.jsonp(url).then(function (res) {
        console.log(res.data.data)

        let tmpArr =res.data.data;
        res.data.data.forEach(function (val,index) {
            tmpArr.forEach(function (v,i) {
                if(v===val){
                    tmpArr.splice(i,1)
                }
            }.bind(this))
        }.bind(this));

        tmpArr.forEach((vl)=>{
            this.$root.$data.arr.push(vl)
        });

        setTimeout(()=>{$('.refresh').removeClass('refreshing');},500)
    }.bind(this)).catch(function (res) {
        console.log(res.state);
        $('.refresh').removeClass('refreshing');
    })

};


