<template>
    <div class="topBar" v-show="tpBarState">
        <div class="bFixed1">
            <router-link to="/message" class="megBox iconfont">&#xe600</router-link>
            <span>今日头条</span>
            <span class="refresh iconfont" >&#xe64c;</span>      <!--$router.go(0)-->
            <router-link to="/search" class="searchBtn iconfont">&#xe501;</router-link>
        </div>
    </div>
</template>
<style>
    @import "../assets/iconfont/iconfont.css";
.bFixed1{
    position: relative;
    height: 6rem;
    background: #D43D3D;
    font-size: 3rem;
    text-align: center;
    color: #fff;
    line-height: 6rem;
    font-weight: bold;
}
    .megBox{
        position: absolute;
        left: 2rem;
        color: #fff;
        font-size: 3rem;
    }
    .refresh{
        padding:.2rem 1.2rem 0 0;
        font-size: 2rem;
        position: absolute;
        color: #fff;
    }
    .searchBtn{
        position: absolute;
        color: #fff;
        font-size: 3rem;
        right: 2rem;
    }

    .refreshing{
        animation:loading 2s linear infinite;
    }
    @keyframes loading{
        0%{transform:rotate(0deg)}
        50%{transform:rotate(180deg)}
        100%{transform:rotate(360deg)}
    }
</style>
<script>
    import {mapGetters} from 'vuex';
    export default{
        computed:mapGetters(
                ['tpBarState']
        ),
        methods:{
            refresh(){
                this.$router.go(0)
            }
        },
    }
</script>




