import recommend from './components/recommend.vue';  //推荐
import video from './components/video.vue';
import hot from './components/hot.vue';
import society from './components/society.vue';                     //社会
import entertain from './components/entertain.vue';             //娱乐
import military from './components/military.vue';                   //军事
import technology from './components/technology.vue';        //科技
import cars from './components/cars.vue';                              //汽车
import sports from './components/sports.vue';
import finance from './components/finance.vue';                       //财经
import international from './components/international.vue';     //国际
import fashion from './components/fashion.vue';
import message from './components/message.vue'

import search from './components/search.vue'        //搜索
import channel from './components/channel.vue'      //频道管理


const routes = [
    {path:'/recommend',component:recommend},
    {path:'/video',component:video},
    {path:'/hot',component:hot},
    {path:'/society',component:society},
    {path:'/entertain',component:entertain},
    {path:'/military',component:military},
    {path:'/technology',component:technology},
    {path:'/cars',component:cars},
    {path:'/sports',component:sports},
    {path:'/finance',component:finance},
    {path:'/international',component:international},
    {path:'/fashion',component:fashion},

    {path:'/search',component:search},           //搜索
    {path:'/channel',component:channel},        //频道管理
    {path:'/',redirect:'/recommend'}               //重定向
];

export default{
    routes
}


